@import "sprite-extends";

//gulp svg sprite bg size mixin
$sprite-width: 2192;
@mixin bgs($icon-svg-w: 1) {
	background-size: ($sprite-width / $icon-svg-w) * 100% auto;
}

/*sprites*/
.menu__arr{
	@extend %svg-angle-down;
	@include bgs(340);
}

.accord__button:after{
	@extend %svg-arrow-alt-circle-down;
	@include bgs(512);
}

.arrow-btn:after{
	@extend %svg-arrow-right;
	@include bgs(448);
}

.plus-btn:after{
	@extend %svg-plus-circle;
	@include bgs(512);
}

.form__btn_search:before{
	@extend %svg-search;
	@include bgs(512);
}

.custom-checkbox__input:checked + .custom-checkbox__label:before{
	@extend %svg-check;
	@include bgs(432);
}

.vk-btn{
	@extend %svg-vk;
	@include bgs(576);
}

.fb-btn{
	@extend %svg-facebook;
	@include bgs(448);
}

.tw-btn{
	@extend %svg-twitter;
	@include bgs(512);
}

.ok-btn{
	@extend %svg-odnoklassniki;
	@include bgs(320);
}

.video__btn-play,
.icon:before{
	@extend %svg-youtube;
	@include bgs(576);
}